App.css

    body {
    font-family:sans-serif;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh;
    color: #dfe22d;
    background-color: #dfe22d;
    
    
  }
  
  .todo-app {
    
    padding: 20px;  
    width: 100px;
    margin-top:3px; 
    color:rgb(54, 39, 121);
  }
  
  h1 {
    font-size:45px;
    text-align: center;
    margin-bottom: 10px;
    color:rgb(47, 22, 105);
  }

  h2{
    font-size: 25px;
    text-align: center;
    color:rgb(47, 22, 105);
  }
  
  input {
    width: calc(30% - 20px);
    padding: 10px;
    margin-bottom: 10px;
    font-size: 20px;
  }
  
  button {
    width: 25%;
    background-color:black;
    color:rgb(181, 199, 218);
    border:none;
    outline:none;
    cursor: pointer;
    font-size: 20px;
    margin-top: 15px;
    margin-left: 100px;
    padding: 10px 5px;
    justify-content: center;
    align-items: center;
    
  }
  
  button:hover {
    background-color:rgb(70, 21, 109);
  }
  
  ul {
    list-style-type: none;
    padding: 0;
  }
  
  li {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    background-color: #f9f9f9;
    margin-bottom: 20px;
    border-radius: 4px;
  }
  
  li button {
    background-color:rgb(42, 94, 65);
    margin-left:80px;
    font-size: 20px;
    padding: 7px 5px;
    cursor: pointer;
  }
  
  li button:hover {
    background-color:rgb(46, 37, 97);
  }
